import React, { Component } from 'react';

export default class extends Component {
  constructor(props){
    super(props);
  }


  componentDidMount() {

  }


  render() {
    return (
        <div className="cadre">
          <div className="img"></div>
          <div className="title">{this.props.anime.title}</div>
        <style jsx>{`
            .cadre {
              width: 225px;
              height: 318px;
              position: relative;
              margin-bottom: 15px;
            }
            .img {
              width: 100%;
              height: 100%;
              background: url('${this.props.anime.image_url}')
            }
            .title {
              position: absolute;
              bottom: 0;
              text-align: center;
              width: 100%;
              background-color: #111111e6;
            }
        `}</style>
        </div>
    );
  }
}
